@import 'templateGrid3';
main header {
  background-color: white;
  padding: 2rem 0;
  text-align: center;
}
main header img {
  display: inline;
  margin: 0 0 1rem 0;
}
main header h1 {
  font-size: clamp(1.2rem, 2.5vw, 2.4rem);
}
main p {
  width: 30rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 1rem;
}