@import 'foundation';
:root {
  --max-width: 112.5rem;
}
html {
  font-size: 1em;
  font-family: CenturyGothic, Helvetica, Arial, sans-serif;
  color: black;
}
body {
  height: 100%;
  min-height: 100%;
  overflow: auto;
  background-color: blue;
  display: grid;
  grid-template-columns: minmax(10px, 1fr);
  grid-template-rows: min-content auto auto;
  grid-template-areas: 
    'header'
    'main'
    'footer';
}
body a {
  color: #A63624;
}
li {
  list-style: none;
}
li a, li a:visited, li a:active {
  text-decoration: none;
}
.button {
    border-top: 1px solid #CCCCCC;
    border-right: 1px solid #333333;
    border-bottom: 1px solid #333333;
    border-left: 1px solid #CCCCCC;
}
#bodyContainer {
  

}
#logoRowContainer, #navBarContainer, #mainContainer, #footerBarContainer {
  margin: 0 auto;
}
#logoRow {
  width: 100%;
  grid-area: header;
  background-color: #373627;
  // margin: 0;
  // padding: 0;
  // overflow: scroll;
  // min-height: 100%;
  // z-index: 999;

}
#logoRowContainer {
  max-width: var(--max-width);
  height: 100%;
  color: white;
  padding: .3em 1em;
  display: grid;
  grid-template-columns: 2fr 5fr 2fr;
  align-items: center;
  justify-items: center;
  // overflow: scroll;

}
#logoRow #navToggleLabel {
  justify-self: left;
  font-size: clamp(1.7rem, 6vw, 2.3rem);
}
#logoRowContact, #logoRowSearch {
    display: none;
}
#logoRowLogo {
  justify-self: center;
  min-width: 170px;
}
#logoRowShop {
  justify-self: end;
  min-width: 26px;
}
#navBar {
  display: none;
}
#navPop {
  display: none;
  max-width: var(--max-width);
  // width: 90%;
  margin: 0;
  padding: .4em 0;
  background-color: #373627;
  grid-column: span 3;
  // z-index: 999;
  justify-self: start;

}
#navPop ul li {
  // text-transform: capitalize;
  color: #fff;
  font-size: 1.3rem;
  padding: .4em 2em;
}
#navPop ul li a, #navPop ul li a:visited {
  color: #fff;
}
#navPop ul li a:hover {
  color: #373627;
  background-color: #fff;
}
.navToggle {
  display: none;
}
.navToggle:checked ~ #navPop {
  display: block;
}
#navToggleLabel:hover {
  cursor: pointer;
}
#navPopShopToggle, #navPopServiceToggle, #navPopShopList, #navPopServiceList {
  display: none;
}
#navPopShopToggle:checked ~ #navPopShopList {
  display: block;
}
#navPopServiceToggle:checked ~ #navPopServiceList {
  display: block;
}
#navPopShopToggleLabel:hover, #navPopServiceToggleLabel:hover {
  cursor: pointer;
  background-color: white;
  color:#373627;
}
#searchLabel {
  display: none;
}
.navPopMedia a img {
  background-color: white;
  display: inline;
  margin-right: .5rem;
}
#navPopShopList li {
  font-size: 1.1rem!important;
}
#navPopServiceList li {
  font-size: 1rem!important;
}
main {
  width: 100%;
  overflow: auto;
  grid-area: main;
  background-color: white;
  // border: 1px solid white;
}
#mainContainer {
  max-width: var(--max-width);
  padding: 0 1em;
  height: 100%;
}
#footerBar {
  align-self: start;
  max-height: 2.4rem;
  width: 100%;
  grid-area: footer;
  background-color: #DCDCDB;
}
#footerBarContainer {
  max-width: var(--max-width);
  height: 100%;
  padding: 0 2rem;
}
#footerBar img {
  display: inline;
  border: 0;
}
#footerBar li a {
  padding: .3rem;
  color: #373627;
  text-decoration: none;
  font-size: .8rem;
  font-weight: bold;
  text-transform: uppercase;
}
#footerBar li a:hover {
  background-color: white;
}
.fNormal {
  display: none;
}
.fImportant {
  display: block;
}
.navList {
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
  align-items: center;
  height: 100%;
}
.navList li a {
  padding: .1rem;
}

.searchForm {
  display: inline-flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.searchForm input {
  width: 10rem;
//   border: 0;
  border: .125rem solid white;
  outline: none;
  background-color: #373627;
  margin-right: .1rem;
  padding-left: .3rem;
  color: white;
}
.searchForm button {
  text-decoration: none;
  margin: 0;
  border: 0;
  background-color: transparent;
  appearance: none;
}
.searchForm button:hover {
  cursor: pointer;
}


@media only screen and (min-width: 780px) and (min-height: 600px) {
  body {
    grid-template-rows:  min-content 3rem auto auto;
    grid-template-areas: 
      'header'
      'nav'
      'main'
      'footer';
  }
  #logoRowContainer {
    grid-template-columns: 3fr 5fr 2fr 1fr;
  }
  #logoRowContainer a {
    color: white;
    text-decoration: none;
  }
  #logoRowContainer a:visited {
    color: white;
    text-decoration: none;
  }
  #logoRowContact {
    display: flex;
    column-gap: 1em;
    align-items: flex-end;
    justify-self: start;
    padding-left: 2em;
  }
  #logoRowShop {
    justify-self: end;
    padding-right: 2em;
  }
  #logoRowSearch {
    display: block;
  }
  #logoRowContainer img {
    display: inline;
  }
  #logoRowPhone, #logoRowEmail {
    font-size: clamp(.875rem, 1.7vw, 1.2rem);
  }
  #logoRow label {
    display: none;
  }
  #navBar {
    grid-area: nav;
    display: block;
    width: 100%;
    background-color: #c97737;
  }
  #navBarContainer {
    max-width: var(--max-width);
    padding: .4em 2em;
    height: 100%;
  }
  .navList {
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .navList li a, .navList li a:visited {
    padding: .6rem;
    color: #1e1e1b;
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
  }
  .navList li a:hover {
    background-color: white;
  }
  #navPop {
    display: none;
  }
  #footerBar li a {
    font-size: clamp(.6rem, 1.3vw, .8rem);
  }
  .fNormal {
    display: block;
  }
  #footerBarSocialMedia {
    display: grid;
    justify-content: center;
    grid-template-columns: auto auto auto;
  }
}